import React, {useState} from 'react';
import './App.css';
import Selection from './components/Selection';
import Button from '@material-ui/core/Button';

import {createMuiTheme, ThemeProvider} from '@material-ui/core/styles';
import MusicSelect from "./components/MusicSelect";
import SwitchMicMusic from "./components/SwitchMicMusic";
import Share from "./components/Share";
import {usePageVisibility} from 'react-page-visibility';

function App() {
    const [reload, setReload] = useState(0);
    const isVisible = usePageVisibility();

    global.refreshCanvas = () => {
        setReload(reload + 1);
    };

    const theme = createMuiTheme({
        palette: {
            type: 'dark',
        },
    });

    if (isVisible) {
        return (
            <div className="App">
                <h1><a style={{textDecoration: "none", color: "inherit"}} href="https://soundvisualiser.com">Sound
                    Visualizer</a></h1>
                <iframe key={reload} src="./visualizer/index.html" title={reload}/>
                <div key={"l" + reload} className="troubleshooting">
                    If the canvas doesn't load, try the following:
                    <ol>
                        <li>Press Reload Canvas</li>
                        {localStorage.getItem('musicData') !== null ? <li><b>Select your sound file again</b></li> :
                            <li>Check microphone permissions for your browser in your OS's system</li>}
                        <li>Disable any adblocker that might cause an interference</li>
                        <li>Make sure that you don't have any javascript blockers active</li>
                    </ol>
                </div>
                <ThemeProvider theme={theme}>
                    <div className="big-container">
                        <div id="full-screen">
                            <Button className="bottom-button" variant="outlined" style={{marginBottom: "20px"}}
                                    onClick={() => setReload(reload + 1)}>reload
                                Canvas</Button> &nbsp;
                            <Button className="bottom-button" variant="outlined" style={{marginBottom: "20px"}}
                                    href="/visualizer" target="_blank"> Open
                                Visualizer in
                                Fullscreen</Button> &nbsp;
                            <Button className="bottom-button" variant="outlined" style={{marginBottom: "20px"}}
                                    onClick={() => {
                                        setReload(reload + 1);
                                        if (localStorage.getItem('musicData') !== null) {
                                            URL.revokeObjectURL(localStorage.getItem('musicData'))
                                        }
                                        localStorage.clear();
                                    }}> Reset Visualizer</Button>
                            <MusicSelect key={"m" + reload}/>
                            <SwitchMicMusic key={"s" + reload}/>
                            <p>&nbsp;</p>
                        </div>
                    </div>
                    <div className="big-container">
                        <Selection key={"g" + reload}/>
                    </div>
                    <div className="big-container">
                        <Share/>
                    </div>
                </ThemeProvider>
            </div>
        );
    } else {
        return <div style={{color: "white", textAlign: "center", marginTop: "49vh"}}>
            <h2>Battery Saver active, click <a href="/">here</a> to reload</h2>
        </div>
    }
}

export default App;
